<template>
  <!--  -->
  <div class="wrapper">
    <div class="items">
      <div class="img"></div>
      <span class="text"></span>
      <span class="num">2</span>
    </div>
    <div class="items">
      <div class="img"></div>
      <span class="text"></span>
      <span class="num">2000</span>
    </div>
    <div class="items">
      <div class="img"></div>
      <span class="text"></span>
      <span class="num">230</span>
    </div>
    <div class="items">
      <div class="img"></div>
      <span class="text"></span>
      <span class="num">25</span>
    </div>
    <div class="items">
      <div class="img"></div>
      <span class="text"></span>
      <span class="num">2</span>
    </div>
    <div class="items">
      <div class="img"></div>
      <span class="text"></span>
      <span class="num">3</span>
    </div>
    <!--  -->
    <!-- <div class="btn" @click="gocommand">
      
    </div>
    <div class="btn" @click="gocommand">
      
    </div> -->
    <!-- <div class="btn" @click="gocommand">
      
    </div> -->
  </div>
</template>

<script>
export default {
  props: ['eventId'],
  methods: {
    gocommand () {
      // 
      this.$router.replace({name: 'command', params: { eventId: this.eventId }})
      this.$layer.close(this.$parent.layerId)
    }
  }
}
</script>

<style lang='stylus' scoped>
  .wrapper
    width 100%
    height 100%
    // background red
    .items
      width: 46%;
      height: 24%;
      // background: #008000;
      margin: 12px 0 0 14px;
      float: left;
      display: flex
      .img
        width 47px
        height 51px
        background url('../../assets/images/home/tk-icon01.png') no-repeat;
        margin: 14px 0 0 12px;
      .text
        width: 64px;
        color #88d6ff
        font-size 16px
        margin 12px 0 0px 6px
      .num
        margin 46px 0 0 -64px
        color #ffb94b
    .items:nth-child(2)
      .img
        background url('../../assets/images/home/tk-icon02.png') no-repeat;
    .items:nth-child(3)
      .img
        background url('../../assets/images/home/tk-icon03.png') no-repeat;
    .items:nth-child(4)
      .img
        background url('../../assets/images/home/tk-icon04.png') no-repeat;
    .items:nth-child(5)
      .img
        background url('../../assets/images/home/tk-icon05.png') no-repeat;
    .items:nth-child(6)
      .img
        background url('../../assets/images/home/tk-icon06.png') no-repeat;
    .btn
      width 50px
      height 40px
      background #1354db
      // margin: 282px 0 0 154px;
      float right;
      cursor pointer
      color #82fbff
      line-height 40px
      text-align center
      font-size 20px
    .btn:hover
        color #fff
        .btn:nth-child(2){
          border-top: 1px solid #666
        }
</style>



