<!-- -->
<template>
  <div class="resourcestab-wrap">
    <div class="yvanlist">
      <div class="title"></div>
      <div class="list">
        <ul>
          <li v-for="(list,index) in detailsData" :key="index">
            <label>{{ list.name }}</label>
            <label>{{ list.content }}</label>
          </li>
        </ul>
      </div>
    </div>
    <div class="content content-table">
      <ul class="tab-tilte">
        <li @click="curr = 0" :class="{ active: curr == 0 }"></li>
        <li @click="curr = 1" :class="{ active: curr == 1 }"></li>
        <li @click="curr = 2" :class="{ active: curr == 2 }"></li>
      </ul>
      <div class="tab-content">
        <div v-show="curr == 0">
          <div class="table-content">
            <el-table
              @selection-change="handleSelectionChange"
              :row-key="grtRowKeys"
              ref="multipleTable"
              :data="tableDatajg"
              style="width: 100%"
              :show-header="showHeader"
            >
              <el-table-column
                type="selection"
                :reserve-selection="true"
                label=""
                align="center"
              ></el-table-column>
              <el-table-column prop="infoName" label="" align="center"></el-table-column>
              <el-table-column prop="infoDesc" label="" align="center"></el-table-column>
              <el-table-column prop="ownerName" label="" align="center" width="80"></el-table-column>
              <el-table-column prop="ownerPhone" label="" align="center"></el-table-column>
            </el-table>
          </div>
        </div>
        <div v-show="curr == 1">
          <div class="table-content">
            <el-table
              @selection-change="handleSelectionChange1"
              :row-key="grtRowKeys"
              ref="multipleTable1"
              :data="tableDatateam"
              style="width: 100%"
              :show-header="showHeader"
            >
              <el-table-column
                type="selection"
                :reserve-selection="true"
                label=""
                align="center"
              ></el-table-column>
              <el-table-column
                prop="teamName"
                label=""
                width="120"
                :show-overflow-tooltip="true"
                align="center"
              ></el-table-column>
              <el-table-column
                prop="shTeamDesc"
                label=""
                :show-overflow-tooltip="true"
                align="center"
              ></el-table-column>
              <el-table-column
                prop="address"
                label=""
                :show-overflow-tooltip="true"
                align="center"
              ></el-table-column>
              <el-table-column prop="header" label="" align="center"></el-table-column>
              <el-table-column prop="dutyTel" label="" align="center"></el-table-column>
            </el-table>
          </div>
        </div>
        <div v-show="curr == 2">
          <div class="table-content">
            <el-table
              @selection-change="handleSelectionChange2"
              :row-key="grtRowKeys"
              ref="multipleTable2"
              :data="tableDataexpert"
              style="width: 100%"
              :show-header="showHeader"
            >
              <el-table-column
                type="selection"
                :reserve-selection="true"
                label=""
                align="center"
              ></el-table-column>
              <el-table-column prop="name" label="" align="center"></el-table-column>
              <el-table-column prop="expertCode" label="" align="center"></el-table-column>
              <el-table-column prop="shSpecialDesc" label="" align="center"></el-table-column>
              <el-table-column prop="phone" label="" align="center"></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
    <div class="mybutton">
      <el-button type="primary" size="mini" @click="dialogFormAdd()"></el-button>
    </div>
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
// import { getSchemeList, getTissue, getTeam, getExpert } from '../../api/scheme'
// import { reportedEvent } from '../../api/visual'
export default {
  data () {
    return {
      showHeader: true,
      multipleTable: [],
      multipleTable1: [],
      multipleTable2: [],
      multipleAllTable: [],
      curr: 0,
      detailsData: [
        { name: ';', content: '' },
        { name: ':', content: '' },
        { name: ':', content: '' },
        { name: ':', content: '' },
        { name: ':', content: '' }
      ],
      // 
      tableDatajg: [],
      // 
      tableDatateam: [],
      // 
      tableDataexpert: []
    }
  },
  props: ['shName', 'shId', 'handle'], //  
  methods: {

    grtRowKeys (row) {
      return row
    },
    handleSelectionChange (val) {
      this.multipleTable = val //  this.multipleTable  
    },
    handleSelectionChange1 (val) {
      this.multipleTable1 = val //  this.multipleTable1  
    },
    handleSelectionChange2 (val) {
      this.multipleTable2 = val //  this.multipleTable2  
    },
    async addScheme (schemeObj) {
      // 
      let schemeConditions = [{ Property: 'Id', Oper: 'eq', Value: this.shId + '' }]
      let schemeItem = builderParams.get('scheme', '', schemeConditions)

      const scheme = await post({ url: builderParams.getUrl(), data: schemeItem })
      if (scheme.Result === 0) {
        if (scheme.Data.length < 1) {
          var parmeter = {
            id: schemeObj.id,
            shName: schemeObj.shName,
            shOrgId: schemeObj.shOrgId,
            shType: schemeObj.shType,
            shFileUrl: schemeObj.shDeplyFileUrl,
            shEventType: schemeObj.shEventType,
            shVersion: schemeObj.shVersion,
            shCreateTime: schemeObj.shCreateTime,
            shCreateUser: schemeObj.shCreateUser,
            shExplain: schemeObj.shExplain,
            storageTime: this.getNowTime()
          }
          let schemeItem = builderParams.save('scheme', parmeter)
          const result = await post({ url: builderParams.execUrl(), data: parmeter })
        }
      }
    },
    async dialogFormAdd () {
      //  
      if (this.handle === '') {
        alert('')
        return null
      }
      this.multipleAllTable.splice(0, 0, this.multipleTable, this.multipleTable1, this.multipleTable2)
      var table = []
      // 
      if (this.multipleAllTable[0].length > 0) {
        let enterp = {}
        this.multipleAllTable[0].forEach(item => {
          enterp = {
            id: this.getuuid(),
            eventId: this.$parent.eventId,
            // 
            enterpId: '',
            // 
            enterpType: '',
            // 
            enterpName: item.infoName,
            // 
            tel: item.ownerPhone,
            // 
            ENTERPDELEGATE: item.ownerName,
            //   1
            matching: 1,
            // 
            createId: '0001',
            // 
            createName: '',
            // 
            createUnitId: '63010000000',
            createunitName: '',
            // id
            schemeId: this.shId
          }
          table.push(builderParams.basesave('enterp', enterp))
        })
      }
      // 
      if (this.multipleAllTable[1].length > 0) {
        let team = {}
        this.multipleAllTable[1].forEach(item => {
          team = {
            id: this.getuuid(),
            eventid: this.$parent.eventId,
            ranksname: item.teamName,
            ranksdesc: item.shTeamDesc,
            person: item.header,
            tel: item.headerTel,
            dispatch: 1,
            dispatchtime: this.getNowTime(),
            createtime: this.getNowTime(),
            //   1
            matching: 1,
            // id
            schemeId: this.shId,
            // 
            createId: '0001',
            // 
            createName: '',
            // 
            createUnitId: '63010000000',
            createunitName: ''
          }
          table.push(builderParams.basesave('ranks', team))
        })
      }
      // 
      if (this.multipleAllTable[2].length > 0) {
        let expert = {}
        this.multipleAllTable[2].forEach(item => {
          expert = {
            id: this.getuuid(),
            eventId: this.$parent.eventId,
            expertType: item.expertCode,
            expertName: item.name,
            tel: item.phone,
            sex: item.sex,
            dispatch: 1,
            dispatchTime: this.getNowTime(),
            createtime: this.getNowTime(),
            //   1
            matching: 1,
            // id
            schemeId: this.shId,
            // 
            createId: '0001',
            // 
            createName: '',
            // 
            createUnitId: '63010000000',
            createunitName: '',
          }
          table.push(builderParams.basesave('exper', expert))
        })
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const reportedEvent = configuration.visual.baseUrl + configuration.visual.reportedEvent
      //  
      var task = {
        // 
        bh: this.getuuid(),
        // 
        xtmc: 'zhdd',
        // 
        jkdz: reportedEvent,
        // 
        ajaxType: 'POST',
        // 
        param: '',
        // 
        state: 0,
        // 
        rksjc: this.getNowTime(),
        // 
        gxsj: null,
        // 
        gxrbh: '',
        // 
        gxr: ''
        // },
        // TableName: 'quartz',
        // WorkType: 'Save'
        //}
      }
      var param = { eventId: this.$parent.eventId, planId: this.shId, name: this.shName }
      task.param = JSON.stringify(param)
      let taskItem = builderParams.save('quartz', task)
      taskItem.DataBase = 'duty'
      const taskResult = await post({ url: builderParams.execUrl(), data: taskItem })
      // 
      let updateConditions = [{ Property: 'Id', Oper: 'eq', Value: this.$parent.eventId }]
      let updateItem = builderParams.update('event', { disposestate: 3 }, updateConditions)
      updateItem.DataBase = 'duty'

      const updateEvent = await post({ url: builderParams.execUrl(), data: updateItem })
      if (updateEvent.Result != 0) {
        alert('')
      }
      // 
      var scheme = {
        id: this.getuuid(),
        eventId: this.$parent.eventId,
        schemeId: this.shId,
        startTime: this.getNowTime(),
        createtime: this.getNowTime(),
        title: '',
        createunitid: '63010000000',
        createunitName: ''
      }
      table.push(builderParams.basesave('SCHEME_RESPONSE', scheme))
      let tranTable = builderParams.tranparams(table)
      // 
      var result = await post({ url: builderParams.tranExecUrl(), data: tranTable })
      if (result.Result === 0) {
        alert('')
        this.$layer.closeAll()
        this.$parent.$parent.loadDispatch()
        this.$parent.updateState(this.shId)
        this.$router.push('/command/?eventId=' + this.$parent.eventId)
      }
      //  
      this.multipleAllTable = []
    },
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    // 
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    async loadPlanInfo () {
      var table = {
        page: 1,
        pageSize: 2,
        name: this.shName
        // type: '',
        // eventType: ''
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const getSchemeList = configuration.scheme.baseUrl + configuration.scheme.getSchemeList
      var result = await get({ url: getSchemeList, data: table })
      if (result.context.status === 200) {
        if (result.data.totalItems > 0) {
          const plan = result.data.resultList[0]
          // 
          this.addScheme(plan)
          this.detailsData[0].content = plan.shName
          // 
          if (plan.shType === 'Common') {
            this.detailsData[1].content = ''
          }
          if (plan.shType === 'Special') {
            this.detailsData[1].content = ''
          }
          if (plan.shType === 'Organization') {
            this.detailsData[1].content = ''
          }
          if (plan.shType === 'Basic') {
            this.detailsData[1].content = ''
          }
          if (plan.shType === 'Unit') {
            this.detailsData[1].content = ''
          }
          if (plan.shType === 'Own') {
            this.detailsData[1].content = ''
          }
          // 
          if (plan.shEventType === 'Natural_Disaster') {
            this.detailsData[2].content = ''
          }
          if (plan.shEventType === 'Accident_Disaster') {
            this.detailsData[2].content = ''
          }
          if (plan.shEventType === 'Public_Health') {
            this.detailsData[2].content = ''
          }
          if (plan.shEventType === 'Social_Security') {
            this.detailsData[2].content = ''
          }
          this.detailsData[3].content = plan.shVersion
          this.detailsData[4].content = plan.shCreateUser
        }
      }
    },
    async loadSchemeSource (type) {
      // id 
      // var schemeId = this.shId
      var schemeId = 82
      if (schemeId) {
        var table = {
          schemeId: schemeId
        }
        var result = {}
        const configuration = await get({ url: 'static/config.json', data: {} })
        if (type === 'jg') {
          this.tableDatajg = []
          // 
          const getTissue = configuration.scheme.baseUrl + configuration.scheme.getTissue
          result = await get({ url: getTissue, data: table })
          if (result.context.status) {
            if (result.data != null) {
              result.data.forEach(item => {
                this.tableDatajg.push({
                  // 
                  infoName: item.infoName,
                  // 
                  infoDesc: item.infoDesc,
                  // 
                  ownerName: item.ownerName,
                  // 
                  ownerPhone: item.ownerPhone
                })
              })
            }
          }
          this.tableDatajg.forEach(item => {
            this.$refs.multipleTable.toggleRowSelection(item, true)
          })
        }
        if (type === 'team') {
          this.tableDatateam = []
          // 
          const getTeam = configuration.scheme.baseUrl + configuration.scheme.getTeam
          result = await get({ url: getTeam, data: table })
          if (result.context.status === 200) {
            if (result.data.length > 0) {
              result.data.forEach(item => {
                this.tableDatateam.push({
                  // 
                  teamName: item.teamName,
                  // 
                  shTeamDesc: item.shTeamDesc,
                  // 
                  address: item.address,
                  // 
                  header: item.header,
                  // 
                  headerTel: item.headerTel,
                  // 
                  dutyTel: item.dutyTel
                })
              })
            }
          }
          this.tableDatateam.forEach(item => {
            this.$refs.multipleTable1.toggleRowSelection(item, true)
          })
        }
        if (type === 'expert') {
          this.tableDataexpert = []
          // 
          const getExpert = configuration.scheme.baseUrl + configuration.scheme.getExpert
          result = await get({ url: getExpert, data: table })
          if (result.context.status === 200) {
            if (result.data.length > 0) {
              result.data.forEach(item => {
                this.tableDataexpert.push({
                  // 
                  name: item.expertName,
                  // 
                  expertCode: item.expertCode,
                  // 
                  sex: item.sex,
                  // 
                  workUnit: item.workUnit,
                  // 
                  shSpecialDesc: item.shSpecialDesc,
                  // 
                  phone: item.telephone
                })
              })
            }
          }
          this.tableDataexpert.forEach(item => {
            this.$refs.multipleTable2.toggleRowSelection(item, true)
          })
        }
      }
    }

  },
  mounted () {
    this.loadPlanInfo()
    this.loadSchemeSource('jg')
    this.loadSchemeSource('team')
    this.loadSchemeSource('expert')
  },
  watch: {
    curr: function (value) {
      // if (value === 0) {
      //   // 
      //   this.loadSchemeSource('jg')
      // } else if (value === 1) {
      //   // 
      //   this.loadSchemeSource('team')
      // } else if (value === 2) {
      //   // 
      //   this.loadSchemeSource('expert')
      // }
    },
    cur: function (value) {
      if (value === 1) {
        this.loadGroup()
      }
    }
  }
}
</script>
<style lang="stylus">
// element ui
.my-input-textarea .el-textarea__inner {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #02e1e8;
  color: #fff;
}

.content-table .el-table {
  color: #fff;
}

.content-table .el-table, .el-table__expanded-cell {
  background-color: transparent;
  // opacity:0;
}

.content-table .el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: #409EFF;
}

.content-table .table-content .el-table td, .el-table th.is-leaf {
  border-bottom: 0px solid #EBEEF5;
}

.content-table .table-content .el-table td:nth-child(1) {
  color: #14CAFA;
}

.content-table .el-table td, .el-table th {
  padding: 4px 0;
  min-width: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  position: relative;
  text-align: left;
}

.content-table .el-table th, .el-table tr {
  background-color: rgba(17, 194, 255, 0.15) !important;
}

// 
.content-table .el-table--border::after, .el-table--group::after, .el-table::before {
  content: '';
  position: absolute;
  background-color: rgba(17, 194, 255, 0.15) !important;
  z-index: 1;
}

// 
.el-table thead {
  color: #fff;
  font-weight: 500;
}

// input
.el-input__inner {
  -webkit-appearance: none;
  background-color: rgba(61, 103, 160, 0.6);
  background-image: none;
  border-radius: 4px;
  border: 1px solid rgba(61, 103, 160, 0.6);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.el-input-group__append, .el-input-group__prepend {
  background-color: #1F97E9;
  color: #fff;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #1F97E9;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
}

.el-tabs__nav-scroll {
  width: 94px;
  overflow: hidden;
}

.el-tabs__item.is-active {
  color: #EBBB2F;
}

.el-tabs__item {
  padding: 0 34px;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  color: #303133;
  position: relative;
}
</style>
<style lang="stylus" scoped>
.resourcestab-wrap {
  width: 100%;
  height: 100%;

  // text-align : center;
  .yvanlist {
    width: 100%;
    height: 40%;

    // background-color red
    .title {
      height: 40px;
      line-height: 40px;
      text-align: center;
    }

    .list ul li {
      padding: 14px 0 0 22px;
    }
  }

  .content {
    width: 96%;
    height: 63%;
    color: #fff;
    font-size: 16px;
    margin: 24px 0 0 3%;

    // overflow-y: scroll;
    // background: url('../../assets/images/home/right_two.png') no-repeat;
    .tab-tilte {
      width: 100%;
    }

    .tab-tilte li {
      float: left;
      width: 24.3%;
      height: 10px;
      line-height: 10px;
      padding: 10px 0;
      text-align: center;
      // background-color: #507696;
      cursor: pointer;
      color: #00B3C0;
    }

    /*  */
    .tab-tilte .active {
      background-color: rgba(247, 158, 20, 0.1);
      color: #F99F11;
      border: solid 1px #F69D14;
    }

    .tab-content div {
      float: left;
      width: 100%;
      margin: 4px 0 0 0;

      .table-content {
        width: 98%;
        height: 260px;
        margin: 2px 0 0 4px;
        border: solid 1px #217ca0;
        background: rgba(17, 194, 255, 0.15);
        overflow-y: scroll;
      }
    }
  }

  .mybutton {
    width: 100%;
    text-align: center;
  }
}
</style>



